/* Gallery container */
.gallery-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  
  .gallery-item {
    border-radius: 0px;
    overflow: hidden;
    transition: transform 0.3s ease-in-out;
  }
  
  .gallery-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
  
  
  .item-1 { grid-column: span 2; grid-row: span 1; }
  .item-2 { grid-column: span 1; grid-row: span 1; }
  .item-3 { grid-column: span 1; grid-row: span 1; }
  .item-4 { grid-column: span 1; grid-row: span 2; }
  .item-5 { grid-column: span 2; grid-row: span 1; }
  .item-6 { grid-column: span 1; grid-row: span 1; }
  .item-7 { grid-column: span 1; grid-row: span 1; }
  .item-8 { grid-column: span 1; grid-row: span 2; }
  .item-9 { grid-column: span 2; grid-row: span 1; }
  .item-10 { grid-column: span 1; grid-row: span 1; }
  .item-11 { grid-column: span 1; grid-row: span 1; }
  .item-12 { grid-column: span 1; grid-row: span 1; }
  
  /* Hover effect */
  .gallery-item:hover {
    transform: scale(1.05);
  }
  
  /* Responsive Design */
  @media (max-width: 1024px) {
    .gallery-container {
      grid-template-columns: repeat(2, 1fr);
    }
  
    .gallery-item {
      grid-column: span 1 !important;
      grid-row: span 1 !important;
    }
  }
  
  @media (max-width: 768px) {
    .gallery-container {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  